import { makeStyles } from "@material-ui/core";
import { green } from "@material-ui/core/colors";

export default makeStyles(() => ({
  modalOverlay: {
    position: "absolute",
    top: 0,
    left: 0,
    zIndex: 101,
    width: "100%",
    height: "100%",
    backgroundColor: "#000",
    opacity: 0.5,
  },
  root: {
    position: "absolute",
    top: "50%",
    left: "50%",
    width: "400px",
    zIndex: 102,
    transform: "translate(-50%, -50%)",
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    alignItems: "center",
    borderRadius: 15,
  },
  option: {
    borderTop: "1px solid #d9d9d9",
    width: "100%",
    textAlign: "center",
    padding: "15px 0",
    fontSize: 14,
    cursor: "pointer",
    fontWeight: "bold",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
  iconContainer: {
    display: "flex",
    alignItems: "center",
    flex: "1.1 0 0px",
  },
  icon: {
    marginRight: 10,
  },
  checkIcon: {
    fontSize: 14,
    marginLeft: 5,
    color: green[500],
  },
}));
